/* eslint-disable @typescript-eslint/naming-convention */
import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { EventsService } from 'src/app/services/events.service';
import { LocalService } from 'src/app/services/local.service';
import { RestService } from 'src/app/services/rest.service';
import { UtilityService } from 'src/app/services/utility.service';

@Component({
  selector: 'app-cleanerschedules',
  templateUrl: './cleanerschedules.page.html',
  styleUrls: ['./cleanerschedules.page.scss'],
})
export class CleanerschedulesPage implements OnInit {
  day: any = 'mon';
  start: any = '06:00:00';
  end: any = '18:00:00';
  segment: any = 'mon';
  loggedCleaner: any;
  schedules: any = [];
  available: any = false;
  constructor(
    public localService: LocalService,
    public restService: RestService,
    public utilityService: UtilityService,
    public eventsService: EventsService,
    private router: Router,
    private activatedRoute: ActivatedRoute
  ) {
    this.loggedCleaner = this.localService.getCleaner();
    if (this.loggedCleaner.available === 'yes') {
      this.available = true;
    } else {
      this.available = false;
    }
    this.getSchedules();
  }

  ngOnInit() { }

  upadateSchedule() {
    this.utilityService.loadingOpen('Updating...');
    this.restService
      .postData('schedule/create', {
        start: this.start,
        end: this.end,
        day: this.day,
        cleaner_id: this.loggedCleaner.id,
      })
      .subscribe(
        (data) => {
          this.getSchedules();
          this.utilityService.loadingClose();
        },
        (err) => {
          this.utilityService.loadingClose();
        }
      );
  }

  delete(id) {
    this.restService
      .postData('schedule/delete', {
        cleaner_id: this.loggedCleaner.id,
        id: id,
      })
      .subscribe((data) => {
        this.getSchedules();
      });
  }

  getSchedules() {
    this.restService
      .getData('cleaner/' + this.loggedCleaner.id + '/schedule')
      .subscribe(
        (data) => {
          this.schedules = data;
        },
        (err) => {}
      );
  }

  changeAvailability(event) {
    console.log(event);
    let tempavialable;
    if (event === true) {
      tempavialable = 'yes';
    } else {
      tempavialable = 'no';
    }
    this.restService
      .postData('cleaner/update_profile', {
        id: this.loggedCleaner.id,
        available: tempavialable,
      })
      .subscribe(
        (data) => {
          this.localService.setCleaner(data);
          this.available = event;
        },
        (err) => {}
      );
  }

  onChangeDay() {
    this.segment = this.day;
  }

  onChangeSegment() {
    this.day = this.segment;
  }

  openNotifications() {
    this.router.navigate(['/cleanernotifications']);
  }
}
